.servicesContainer{
    display: flex;
    position: relative;
/*    width: 80%;*/
    margin-top: 16px;
    margin-left: auto;
    margin-right: auto;
}

.servicesAnchor{
    display: block;
    position: absolute;
    top: -60px;
    visibility: hidden;
}

@media screen and (max-width: 639px){
    .servicesContainer{
        width: 95%;
    }
}

@media screen and (min-width: 640px) and (max-width: 800px){
    .servicesContainer{
        width: 630px;
    }
}

@media screen and (min-width: 801px){
    .servicesContainer{
        width: 80%;
    }
}